@use '../../../variables' as var;

.CustomButton {
  position: relative;
  min-width: 60px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 12px;
  height: 45px;
  padding: 0 25px;
  background-image: var.$color-button-gradient;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  z-index: 0;
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.gray {
  background: #231f4f;
}

.secondary {
  background-image: var.$color-button-gradient;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: white;
  border-radius: 10px;
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    inset: 0;
    border: 2px solid transparent;
    border-radius: 10px;
    background: inherit;
    background-origin: border-box;
    background-clip: border-box;
    -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    -webkit-mask-repeat: no-repeat;
  }
}
.rounded {
  border-radius: 100px;
  &::before {
    border-radius: 100px;
  }
}
.disabled,
.loading {
  background-color: #4e4b72;
  background-image: none;
  color: rgba(#fff, 0.5);
  pointer-events: none;
}

@media (max-width: var.$mobile-break-point) {
  .CustomButton {
    font-size: 14px;
  }
}
