@use '../../variables' as var;

@mixin flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Coming {
  @include flex;

  // align-items: flex-start;

  max-width: 1400px;
  height: 670px;
  margin: 50px auto;
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  // padding-top: 60px;

  justify-content: center;

  img {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    object-fit: cover;
    height: 100%;
  }
}

.CountDown {
  @include flex;

  justify-content: center;
  width: 351px;
  height: 70px;
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 40px;
  color: #fff;

  span {
    margin-right: 10px;
    font-weight: 500;
    font-size: 20px;
  }

  :global {
    .ant-statistic-content-value {
      color: rgba(255, 205, 29, 1);
      font-size: 32px;
      font-weight: 700;
    }
  }
}

.ComingWarp {
  position: relative;
  z-index: 1;
}
.learnMoreLink {
  color: white;
  text-decoration: none;
  font-size: 24px;
  font-weight: 700;
}

.Participate {
  padding: 100px 15px 0;
  padding-bottom: 1px;
  background: #1e1430;
}

.ParticipateDesc {
  max-width: 1000px;
  // min-height: 90px;
}

.ParticipateCard {
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 24px;
}

.ParticipateBuy {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  max-width: 1000px;
  min-height: 557px;
  margin: 0 auto;
  color: #fff;
  margin-top: 20px;
  margin-bottom: 50px;

  img {
    width: 480px;
    height: auto;
  }
}

.ParticipateBuyWarp {
  display: flex;
  justify-content: center;
  align-items: stretch;
  width: 100%;
  gap: 10px;
}

.ParticipateBuyFormTitle {
  gap: 10px;
  h3 {
    font-weight: 700;
    font-size: 24px;
  }
}

.ParticipateBuyFormDesc {
  font-weight: 400;
  font-size: 14px !important;
  color: #ffffff;
  text-align: left !important;
  align-self: flex-start;
}

.ParticipateBuyImageWarp {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;

  video {
    width: 80%;
    max-width: 580px;
    border-radius: 10px;
  }
  // padding: 38px 40px 0 0;
}

.BuyButton {
  width: 100%;
  height: 55px;
  .buyButtonContent {
    line-height: 18px;
    span {
      font-size: 12px;
      color: #ffffff;
      opacity: 0.5;
    }
  }
}

.ParticipateBuyForm {
  max-width: 500px;
  // margin-right: 15px;
  // min-width: 45%;
  width: 45%;
  display: grid;
  gap: 10px;
}

.PaddingWarp {
  display: grid;
  gap: 10px;
  padding: 22px 0px 0px 50px;
}

.Stage,
.Price,
.Cost {
  @include flex;
}

.Price {
  :global {
    .ant-select {
      color: white;
      font-weight: 700;
      font-size: 16px;
    }
    .ant-select-arrow {
      color: white;
    }
    .ant-select-selection-item {
      font-weight: 700;
      display: flex;
      align-items: center;
      img {
        width: 20px;
        margin-right: 8px;
      }
    }
  }
}
:global {
  .currencyDropdown {
    background: #1e1930 !important;
    border-radius: 24px !important;
    padding: 5px !important;

    .ant-select-item {
      color: white !important;
      background: transparent !important;
      border-radius: 24px !important;
    }
    .ant-select-item-option-active,
    .ant-select-item-option-selected {
      background: #171328 !important;
      color: white !important;
    }

    .ant-select-item-option-content {
      font-weight: 700 !important;
      font-size: 16px !important;
      display: flex;
      align-items: center;
      img {
        width: 20px !important;
        margin-right: 8px !important;
      }
    }
  }
  // .ant-select-single .ant-select-selector .ant-select-selection-item,
  // .ant-select-single .ant-select-selector .ant-select-selection-placeholder {
  //   line-height: 32px;
  //   font-size: 16px;
  // }
}

.StageCountdown {
  display: flex;
  align-items: center;
  gap: 10px;
  :global {
    .ant-statistic-content-value {
      color: white;
    }
  }
}

.Cost {
  text-align: right;
  justify-content: flex-end;
}

.StageTag {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(102.16deg, #231f4f 0%, #332d4f 100%);
  border-radius: 24px 8px 48px 0px;
  min-width: 200px;
  height: 64px;
  color: #ffcd1d;
  font-weight: 700;
  font-size: 20px;
  padding: 0 20px;
  margin-bottom: 10px;

  span {
    font-weight: 700;
    font-size: 16px;
    text-align: right;
    color: #ffffff;
    display: inline-block;
    margin-left: 10px;
  }
}

.StageServings {
  color: #9692a9;
  font-weight: 700;
  font-size: 16px;
}

.StageServingsActive {
  color: #ffcd1d;
}

.Attention {
  text-align: left;
  display: grid;
  gap: 10px;
  background: rgba($color: #16122d, $alpha: 0.2);
  border-radius: 12px;
  padding: 18px 22px;
  color: #9644b4;
  display: grid;
  gap: 10px;
  width: 93%;
  margin: 40px auto;
}

.Attention2 {
  text-align: left;
  display: grid;
  gap: 10px;
  background: rgba($color: #16122d, $alpha: 0.2);
  border-radius: 12px;
  padding: 18px 22px;
  color: #9644b4;
  display: grid;
  gap: 10px;
  margin-top: 32px;
}

.AttentionDesc {
  display: grid;
  gap: 10px;
}

.AttentionTitle {
  @include flex;
  justify-content: flex-start;
  gap: 15px;
  font-weight: 700;
  font-size: 16px;
  img {
    width: 20px;
  }
}

.ThemisGavel {
  @include flex;

  align-items: stretch;
  max-width: 1000px;
  margin: 50px auto;
  text-align: left;
  gap: 20px;
  video {
    width: 40%;
    border-radius: 10px;
  }
}

.ThemisGavelTitle {
  text-align: left !important;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
  // margin-top: 30px;
}

.ThemisGavelInfoWarp {
  width: 60%;
  // margin-left: 10px;
  color: #fff;

  h3 {
    font-size: 25px;
  }

  p {
    font-size: 14px;
  }
}

.BlueText {
  color: #4775fa;
}

.BlueBg {
  background: rgba($color: #3e44cf, $alpha: 0.2);
}

.Invite {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  border-radius: 10px;
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 50px;
  min-height: 200px;
  position: relative;
  padding: 30px 0 30px 30px;
  color: #fff;
}

.InviteTitle {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 140%;
  gap: 15px;
  @media (max-width: var.$mobile-break-point) {
    font-size: 28px;
  }
}

.InviteTextBg {
  position: absolute;
  width: 58%;
  height: auto;
  left: 36px;
  top: 22px;
}

.InviteLeft {
  text-align: left;
}

.InviteRight {
  @include flex;
  justify-content: flex-end;
}

.InviteContentWarp {
  @include flex;
  align-items: center;
  position: relative;
  justify-content: flex-start;
  z-index: 1;
}

.InviteLogo {
  margin: 10px 0;
}

.InviteAwesome {
  width: 100px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.buyButton {
  background: white;
  color: #3e44cf;
  height: 45px;
  padding: 0 20px;
  margin-right: 20px;
}

.buyButtonText {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.ProtocolText {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-left: 10px;
}

.ProgressBarWarp {
  max-width: 1000px;
  margin: 0 auto;
}

.ProgressBar {
  max-width: 100%;
  height: 10px;
  margin: 0 auto;
  background: rgba($color: #2a2e52, $alpha: 0.5);
  border-radius: 10px;
}

.Begins {
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  margin: 10px 0 10px;
}

.ProgressBarActive {
  // width: 60%;
  width: 0;
  background: linear-gradient(90deg, #bb6ce6 0%, #ffcd1d 100%);
  border-radius: 10px;
  height: 10px;
  transition: all 0.5s linear;
}

.Rrewards {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 302px;
  height: 47px;
  background: linear-gradient(277.23deg, #1d93cb 0%, #3e44cf 100%);
  border-radius: 23.5px;
  font-size: 20px;
  flex: 0 0 302px;

  img {
    margin-right: 10px;
  }
}

.Protocol {
  display: flex;
  flex-direction: column;
  color: #fff !important;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;

  :global {
    .ant-checkbox-wrapper {
      color: #fff;
      margin: 3px 10px;
    }
  }
}

@media (max-width: var.$mobile-break-point) {
  .Attention {
    margin: 20px auto;
  }
  .Attention2 {
    margin-top: 20px;
  }
  .Coming {
    height: 360px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .Participate {
    padding: 80px 15px 1px;
  }
  .ParticipateBuy {
    flex-wrap: wrap;
    // padding-bottom: 10px;
    margin-bottom: 30px;
  }
  .ParticipateBuyWarp {
    flex-direction: column;
    align-items: center;
  }

  .ParticipateBuyForm {
    max-width: 100%;
    width: 100%;
    // padding-right: 20px;
    padding: 10px;
  }

  .PaddingWarp {
    padding-left: 20px;
  }

  .ParticipateBuyImageWarp {
    width: 100%;
  }

  // .ParticipateBuyImageWarp {
  //   @include flex;

  //   padding: 0;
  //   width: 100%;
  //   justify-content: center !important;
  //   align-items: center !important;
  //   margin-top: 30px;

  //   min-height: auto;

  //   display: none;

  //   video {
  //     width: 100%;
  //   }
  // }

  .StageTag {
    font-size: 14px;
    height: 55px;
    span {
      display: block;
      font-size: 12px;
    }
  }

  .ThemisGavel {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
    margin-top: 30px;
    video {
      width: 80%;
    }
  }

  .ThemisGavelInfoWarp {
    width: 100%;
    // margin-top: 20px;
  }

  .InviteContentWarp {
    flex-wrap: wrap;
  }

  .InviteTitle {
    flex-wrap: wrap;
  }

  .InviteLeft,
  .InviteRight {
    width: 100%;
  }

  .InviteRight {
    justify-content: flex-start;
  }

  .InviteLeft {
    position: relative;
    z-index: 1;
  }

  .InviteAwesome {
    top: 30%;
    // position: absolute;
    // top: -20px;
    // right: 0;
  }

  .InviteTitleText {
    width: 100%;
  }

  .InviteDesc {
    width: 250px;
    margin: 10px 0;
  }

  .ThemisGavelTitle {
    align-items: center;
    h3 {
      font-size: 24px;
      text-align: center;
    }
  }
}

.loadingPlaceholder {
  width: 100%;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
