@use '../../../variables' as var;
.CustomModal {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    // background-color: rgba(#070b2d, 1);
    background-color: #060609;
    border-radius: 30px;
    padding: 0;
    overflow: hidden;
  }
  :global(.ant-modal-body) {
    padding: 0;
    color: white;
  }
}
