@use '../../variables' as var;

.LearnMore {
  padding: 25vw 0 0 0;
  background-color: #1c152e;
  background-blend-mode: screen;
  background-position: center -150px;
  background-size: 100% auto;
  background-repeat: no-repeat;
}

.EcosystemTitleSec {
  margin-top: 50px;
  h2 {
    font-size: 48px;
  }
}
.Card {
  background: linear-gradient(
    180deg,
    #100a1e 0%,
    #181129 26.49%,
    #1c1330 50.11%,
    rgba(28, 20, 46, 0) 100%
  );
  border-radius: 24px;
  width: fit-content;
  // max-width: 1400px;
  margin: 0 auto;
  padding: 50px;
}

.CorePhilosophWarp {
  display: flex;
  justify-content: space-between;
  max-width: 1149px;
  margin: 0 auto;
  margin-top: 60px;
}

.CorePhilosophItem {
  width: 50%;
  color: #fff;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 30px;
  gap: 40px;
  max-width: 700px;
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 30px;
  margin: 0 20px;
}

.CorePhilosophItemContentWarp {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}

.CorePhilosophItemTitle {
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
  margin: 15px 0;
}

.CorePhilosophItemContent {
  color: rgba($color: #ffffff, $alpha: 0.5);
}

.EcosystemMap {
  // background: #1e1430;
  padding: 0 10px 60px;
  // margin-top: 120px;

  img {
    width: 100%;
    max-width: 1140px;
  }
}

.Infrastructure {
  background: #26153f;
  padding: 80px 10px;
  padding-top: 1px;
}

.InfrastructureDesc {
  color: #fff;
  text-align: left;
  max-width: 1150px;
  margin: 0 auto;
}

.InfrastructureWarp {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 1150px;
  margin: 0 auto;
  margin-top: 50px;
  gap: 35px;
}

.InfrastructureItem {
  color: #fff;
  width: 100%;

  img {
    width: 80%;
  }
}

.InfrastructureItemTitle {
  margin-top: 20px;
  h2 {
    font-size: 20px;
    min-height: 80px;
  }
}

.InfrastructureItemContent {
  font-size: 14px;
  color: rgba($color: #ffffff, $alpha: 0.5);
  text-align: justify;
}

