@media (max-width: var.$mobile-break-point) {
  .LearnMore {
    background-position: center -50px;
  }
  .CorePhilosophWarp {
    flex-wrap: wrap;
  }

  .CorePhilosophItem {
    width: 100%;
    margin-bottom: 20px;
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }
  .EcosystemTitleSec {
    margin-top: 30px;
    h2 {
      font-size: 18px;
    }
  }
  .InfrastructureWarp {
    margin-top: 30px;
    width: 95%;
  }
  .Card {
    // max-width: 1400px;
    margin: 0 auto;
    padding: 30px 10px;
  }

